
body{
  font-family: "SF Pro Display", sans-serif;
  background-color: $bg-color;
}

[class*=col]{
  padding: 0;
}

html{
  font-size: 10px;
  @include small-screen{
    font-size: 12px;
  }
  @include medium-screen{
    font-size: 14px;
  }
  @include xlarge-screen{
    font-size: 16px;
  }
}